<template>
<page-layout :title="page_title">
  <div class="newSystAnnouncement transparent-background">
    <a-form
      @submit="handleSubmit"
      :form="form"
    >
      <a-card
        title="公告设置"
        :bordered="false"
        :headStyle="{padding:'0 24px'}"
        :body-style="{padding:'0'}"
      >
        <a-row style="padding:24px">
          <a-col
            class="gutter-row"
            :xl="12"
            :lg="12"
            :md="12"
          >
          <a-form-item label="标题" style="margin-right:24px">
            <a-input
              placeholder="请输入"
              v-decorator="['title',{initialValue:title,rules: [{ required: true, message: '该项是必填项' }]}]"
            />
          </a-form-item>
          </a-col>
          <a-col
            class="gutter-row"
            :xl="12"
            :lg="12"
            :md="12"
          >
          <a-form-item label="标志">
            <a-select
              placeholder="请选择"
              v-decorator="['tag',{initialValue:tag,rules: [{ required: true, message: '该项是必填项' }]}]"
            >
              <a-select-option value="1">普通</a-select-option>
              <a-select-option value="2">重要</a-select-option>
            </a-select>
          </a-form-item>
          </a-col>
        </a-row>
      </a-card>
      <a-card
        title="公告内容"
        :bordered="false"
        :headStyle="{padding:'0 24px'}"
        :body-style="{padding:'0'}"
      >
        <a-row style="padding:24px">
          <a-col
            class="gutter-row"
            :xl="24"
            :lg="24"
            :md="24"
          >
          <a-form-item>
            <wangeditor
              :catchData="catchData"
              v-decorator="['content',{initialValue:content}]"
            ></wangeditor>
          </a-form-item>
          </a-col>
        </a-row>
      </a-card>
      <a-form-item :wrapperCol="{ span: 12, offset: 5 }">
        <a-button
          type='primary'
          style="margin-right: 8px"
          htmlType='submit'
        >
          保存
        </a-button>
        <a-button
          @click="handleReset"
        >
          重置
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</page-layout>
</template>

<script>
import AForm from 'ant-design-vue/lib/form'
import ACard from 'ant-design-vue/lib/card'
import ARow from 'ant-design-vue/lib/row'
import ACol from 'ant-design-vue/lib/col'
import AInput from 'ant-design-vue/lib/input'
import ASelect from 'ant-design-vue/lib/select'
import AButton from 'ant-design-vue/lib/button'
import wangeditor from "@/components/Tools/Wangeditor";
import PageLayout from "@/components/Page/PageLayout";
const AFormItem = AForm.Item;
const ASelectOption = ASelect.Option;
export default {
  components: {
    AForm,
    ACard,
    ARow,
    ACol,
    AInput,
    ASelect,
    ASelectOption,
    AButton,
    AFormItem,
    wangeditor,
    PageLayout
  },
  data() {
    return {
      title: "",
      tag: "",
      content: ""
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  computed:{
    page_title() {
      return this.$route.meta.title;
    },
  },
  methods: {
    catchData(value) {
      this.content = value; //在这里接受子组件传过来的参数，赋值给data里的参数
    },
    handleSubmit(e) {
      e.preventDefault();
      let _this = this;
      this.form.validateFields((err, values) => {
        if (!err) {
          alert("Received values of form: " + JSON.stringify(values));
        }
      });
    },
    handleSelectChange(value) {
      this.form.setFieldsValue({
        customerName: "heiheihei"
      });
    },
    handleReset() {
      this.form.resetFields();
      //        debugger;
      //        this.form.setFieldsValue({
      //          customerName: 'heiheihei',
      //        })
    }
  }
};
</script>

<style>
.newSystAnnouncement .ant-form-item-label {
  text-align: left;
}
.newSystAnnouncement .ant-form-item-control-wrapper {
  flex: 1;
  text-align: center;
}
.newSystAnnouncement .ant-card {
  margin-bottom: 24px;
}
@media (min-width: 992px) {
  .newSystAnnouncement .ant-col-xl-4 {
    width: 20%;
  }
}
</style>



